import { Inter } from "next/font/google";
import "./globals.css";
import { AuthProvider } from "@/contexts/AuthContext";
import Header from "@/app/components/Header";
import { getCurrentUser } from "@/lib/session";

const inter = Inter({ subsets: ["latin"] });

export const metadata = {
  title: "Veterinaria Magalí Martin",
  description: "Gestión de turnos, adopciones y tienda online para tu mascota.",
};

export default async function RootLayout({ children }) {
  const currentUser = await getCurrentUser();

  const userDataForHeader = currentUser ? {
    isLoggedIn: true,
    name: currentUser.name || currentUser.email,
    email: currentUser.email,
    picture: currentUser.picture,
    role: currentUser.role || null,
  } : {
    isLoggedIn: false,
  };

  return (
    // CORREGIDO: La clase de la fuente se aplica a <html> para evitar el error de hidratación.
    <html lang="es" className={inter.className}>
      <body className={`bg-gray-50 flex flex-col min-h-screen`}>
        <AuthProvider>
          <Header userData={userDataForHeader} />
          <main className="flex-grow w-full">
            {children}
          </main>
        </AuthProvider>
      </body>
    </html>
  );
}
